@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@1&display=swap');

* {
    margin: 0;
    padding: 0;

}
body{
    background: #bcbaba;
    color: #222;
      font-family: "Libre Baskerville", serif;
      font-weight: 300;

}

.wrapper {
    width: 1280px;
    margin: 50px auto;
}
header{
    position:relative;
}
header .logo
{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav
{
    float: right;
    list-style: none;
}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms ease;
}

header ul.nav li:hover
{
    opacity: 0.3;
}
header .shop-cart-button
{
    float: right;
    cursor: pointer;
    transition: color, transform 500ms ease;
}
header .shop-cart-button:hover,
header .shop-cart-button:active{
    color: green;
    transform: scale(1.5);
}
header .shop-cart {
    position: absolute;
    top: 30px;
    right: 0;
    width: 450px;
    background: #fafafa;
    -webkit-box-shadow: 4px 5px 15px -7px #606060; 
box-shadow: 4px 5px 15px -7px #606060;
z-index: 1000;
padding: 20px;
padding-bottom: 0;
}
header .shop-cart .empty h2{
    font-size: 20px;
    margin-bottom: 20px;
}
header .shop-cart .item{
    width: 100%;
    float: left;
    margin-bottom: 20px;
}
header .shop-cart .item img{
    width: 70px;
    float: left;
    margin-right: 20px;
}
header .shop-cart .item h2
{
    font-size: 20px;
    margin-bottom: 10px;
}
header .shop-cart .item p{
    color: #797979;
    font-weight: 600;
}

header .shop-cart .item .delete-icon
{
    color: #ca5252;
    float: right;
    position: relative;
    top: -25px;
    cursor: pointer;
transition: color, transform 500ms ease;
}

header .shop-cart .item .delete-icon:hover
{
    color: #d83030;
    transform: scale(1.5);
}

header .shop-cart .summa {
    float: left;
    width: 100%;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 20px;
}

header .presentation
{
    margin: 50px 0;
    background: url('./img/zoloto.jpg') no-repeat;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: #bcbcbc;
    position: relative;
}
header .presentation::after{
    content: 'The best';
    position: absolute;
    top: 100px;
    left: 50px;
    width: 300px;
    font-size: 40px;
    font-weight: 600;
    color: #fff;

}
header .presentation::before{
    content: 'forever';
    position: absolute;
    top: 143px;
    left: 75px;
    width: 300px;
    font-size: 16px;
    font-weight: 300;
    color: #fff;

}

.categories div {
    display: inline-block;
    background: #f2f2f2;
    border-radius: 50px;
    padding: 10px 20px;
    margin-bottom: 25px;
    margin-right: 15px;
    cursor: pointer;
    border: 1px solid transparent;
    transition: all 350ms ease;

}
.categories div:hover
{
    border-color: silver;
    transform: scale(1.5);
}

.full-item
{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.8);
    z-index: 9999;
    overflow: auto;
}
.full-item > div{
    width: 600px;
    position: relative;
    margin: 10% auto;
    padding: 40px 30px;
    background: #fff;
    border-radius: 9px;
}
main{
    display:flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}
main .item
{
    width: 30%;
    margin-bottom: 50px;
    background: #b0aeae;
    overflow: hidden;
    position: relative;
    padding-bottom: 10px;
}

main .item img, .full-item img{
    width: 100%;
    border-radius: 10px 10px 10px 10px;
    transition: transform 500ms ease;
    cursor: pointer;
}
main .item img:hover, .full-item img:hover{
    transform: scale(1.05);
}
main h2,.full-item h2, main p, .full-item p, main b, .full-item b{
    margin: 10px 20px;
    color: #333;
}
main b, .full-item{
    color: #309440;
}
main .add-to-cart,
.full-item .add-to-cart
{
    position: absolute;
    right: 20px;
    bottom: 5px;
    background: #b0aeae;
    width: 20px;
    height:20px;
    text-align: center;
    line-height: 21px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform 500ms ease;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover{
    transform: scale(2) translateY(+0px);
}


.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 24px;
  color: black;
  transition: transform 500ms ease;
    
}
.close:hover
{
       transform: scale(1.5) translateY(+0px);
}

footer{
    text-align: left;
    margin-top: 100px;
}

.img-container {
  cursor: pointer;
  overflow: hidden;
  position: relative;
}

.particle {
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  animation: particle-fly 2s ease-out forwards, glow 1s infinite alternate;
}

.particle.heart {
  text-shadow: 0 0 8px currentColor, 0 0 15px currentColor, 0 0 25px currentColor;
}

@keyframes particle-fly {
  0% {
    transform: translate(var(--dx-start), var(--dy-start)) scale(0.5);
    opacity: 1;
  }
  30% {
    transform: translate(var(--dx-mid), var(--dy-mid)) scale(1.3);
  }
  60% {
    transform: translate(var(--dx-mid2), var(--dy-mid2)) scale(1.6);
  }
  100% {
    transform: translate(var(--dx-end), var(--dy-end)) scale(1.5);
    opacity: 0;
  }
}

@keyframes glow {
  0% {
    text-shadow: 0 0 8px currentColor, 0 0 15px currentColor, 0 0 25px currentColor;
  }
  50% {
    text-shadow: 0 0 12px currentColor, 0 0 20px currentColor, 0 0 30px currentColor;
  }
  100% {
    text-shadow: 0 0 8px currentColor, 0 0 15px currentColor, 0 0 25px currentColor;
  }
}
